:root {
  --primary-color: #934bf1f1;
  --sidebar-color: #222222fa;
  --background-color: #000000de;
  --sidebar-header-background-color: rgba(0, 0, 0, 0.5);
  --sidebar-item-background-color: rgba(0, 0, 0, 0.35);
  --sidebar-maincontent-padding: 20px;
  --task-item-completed-color: rgba(27, 184, 21, 0.719);
  --task-item-pending-color: rgba(255, 165, 0, 0.719);
  --task-item-overdue-color: rgba(255, 0, 0, 0.719);
  --task-item-default-color: rgba(255, 255, 255, 0.9);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.bodyHomepage {
  overflow: hidden;
  width: 100%;
  height: 100vh;
  font-family: "REM", sans-serif;
}

.homepage {
  width: 100%;
  height: 100vh;
  display: flex;
}

.mainSideBar {
  width: 19%;
  height: 100%;
  background-color: var(--sidebar-color);
  padding: var(--sidebar-maincontent-padding);
}

.mainContent {
  background-color: var(--background-color);
  width: 81%;
  height: 100%;
  padding: var(--sidebar-maincontent-padding);
}

.homeSideBarHeaderContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--sidebar-header-background-color);
  flex-wrap: wrap;
  margin-bottom: 25px;
  padding: 10px;
  border-radius: 10px;
  border-bottom: 2px solid white;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}

.homeSideBarHeader {
  padding: 5px;
}

.homeSideBarHeaderInfo h1 {
  font-size: 1.5rem;
  color: var(--primary-color);
  margin-left: 5px;
}

.homeSideBarHeaderInfo {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.homeSideBarHeaderIcons {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

img {
  filter: brightness(0) invert(1);
}

.homeProfilePicture {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 10px;
  filter: brightness(1) invert(0);
}

.homeBellIcon {
  margin-right: 8px;
}

.homeSideBarItem {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 6.5px;
  border-radius: 10px;
  background-color: var(--sidebar-item-background-color);
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  transform: scale(1) translateY(0);

  transition: background-color 0.2s ease-in-out, transform 0.2s ease-in-out,
    box-shadow 0.2s ease-in-out;
}

.sideBarHeader {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.homeSideBarItems {
  display: flex;
  flex-direction: column;
  gap: 15px;
  scrollbar-width: thin;
  margin: 5px;
}

.homeSidebarItemText {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  color: var(--primary-color);
  font-size: 1.05rem;
}

.homeSidebarItemIconContainerLeft,
.homeSidebarItemIconContainerRight {
  display: flex;
}

.homeSidebarItemIconContainerRight {
  gap: 7px;
}

.homeSideBarItem:hover {
  background-color: #160927be;
  transform: scale(1.03) translateY(-2px);
}

.homeSidebarItemIconContainer {
  width: 95%;
  display: flex;
  position: absolute;
  justify-content: space-between;
}

.deleteProjectIcon {
  padding: 1px;
  width: 22px;
  align-items: flex-start;
  max-height: 22px;
  cursor: pointer;
  background-color: #ffffff00;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 0.5px 5px;
  display: none;

  transform: scale(1) translateY(0);

  transition: background-color 0.2s ease-in-out, transform 0.2s ease-in-out,
    box-shadow 0.2s ease-in-out;
}

.homeSideBarItem:hover .deleteProjectIcon {
  display: inline-block;
}

.editProjectIcon {
  padding: 1px;
  width: 22px;
  align-items: flex-start;
  max-height: 22px;
  cursor: pointer;
  background-color: #ffffff00;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 0.5px 5px;
  display: none;

  transform: scale(1) translateY(0);

  transition: background-color 0.2s ease-in-out, transform 0.2s ease-in-out,
    box-shadow 0.2s ease-in-out;
}

.homeSideBarItem:hover .editProjectIcon {
  display: inline-block;
}

.homeSideBarFooter {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  color: rgb(255, 255, 255);
  border-top: 1px solid rgba(255, 255, 255, 0.418);
  padding: 10px 5px 5px 5px;
  margin-top: auto;
}

a {
  text-decoration: none;
  color: var(--primary-color);
}

.homeMainContent {
  padding-top: 10px;
  width: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.homeMainContent::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

.homeMainContent::-webkit-scrollbar-track {
  background: transparent;
}

.homeMainContent::-webkit-scrollbar-thumb {
  background-color: var(--primary-color);
  border-radius: 10px;
}

.homeMainContent::-webkit-scrollbar-button {
  display: none !important;
  width: 0px;
  height: 0px;
  background: transparent;
  border: none;
  padding: 0;
  margin: 0;
  overflow: hidden;
}

.homeMainContentHeader h2 {
  color: var(--primary-color);
  font-size: 1.5rem;
  margin-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.418);
  padding-bottom: 10px;
}

.projectTaskItemHeader h2 {
  color: var(--primary-color);
  font-size: 1.43rem;

  padding-bottom: 10px;
}

.homeMainContentBody > p {
  padding-top: 10px;
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.2rem;
  margin-bottom: 20px;
}

.projectTaskContentContainer > p {
  color: rgb(255, 255, 255);
  font-size: 1.03rem;
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.projectItemContainer
  > .projectTaskDetails:last-child
  .projectTaskContentContainer
  > p {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: none;
}

.projectTaskItem h3 {
  color: var(--primary-color);
  font-size: 1.23rem;
  margin-bottom: 10px;
}

.projectTaskItem {
  display: flex;
  flex-direction: column;
  width: 350px;
  background-color: rgba(255, 255, 255, 0.05);

  border-radius: 10px;
  max-height: 96.5%;
  overflow: hidden;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  border-left: 5px solid var(--task-item-default-color);
}

.homeMainContentBody {
  padding: 10px;
  margin-top: 20px;
  width: 100%;

  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: max-content;
  grid-gap: 20px;
  align-items: start;

  flex-grow: 1;
  min-height: 0;
  overflow-x: auto;
  overflow-y: hidden;
  scrollbar-width: thin;
  scrollbar-color: var(--primary-color) transparent;
}

.homepage {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.projectItemContainer:hover {
  scrollbar-color: var(--primary-color) transparent;
}

.projectItemContainer {
  padding: 15px 20px 0 20px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: transparent transparent;
}

.projectTaskItemHeader {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding-top: 15px;
  margin: 0 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.418);
}

.addTaskButton {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: #0000004d;
  padding: 5px;
  border-radius: 10px;
  color: var(--primary-color);
  font-size: 1.25rem;
  font-weight: 600;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  transform: scale(1) translateY(0);

  transition: background-color 0.2s ease-in-out, transform 0.2s ease-in-out,
    box-shadow 0.2s ease-in-out;
}

.addTaskButton:hover {
  background-color: #160927be;
  transform: scale(1.03) translateY(-2px);
  box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 4px, rgba(0, 0, 0, 0.14) 0px 3px 8px;
}

.projectTaskButtonTargetContainer {
