round-color: rgba(0, 0, 0, 0.5);\n  --sidebar-item-background-color: rgba(0, 0, 0, 0.35);\n  --sidebar-maincontent-padding: 20px;\n  --task-item-completed-color: rgba(27, 184, 21, 0.719);\n  --task-item-pending-color: rgba(255, 165, 0, 0.719);\n  --task-item-overdue-color: rgba(255, 0, 0, 0.719);\n  --task-item-default-color: rgba(255, 255, 255, 0.9);\n}\n\n* {\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0;\n}\n\n.bodyHomepage {\n  overflow: hidden;\n  width: 100%;\n  height: 100vh;\n  font-family: \"REM\", sans-serif;\n}\n\n.homepage {\n  width: 100%;\n  height: 100vh;\n  display: flex;\n}\n\n.mainSideBar {\n  width: 19%;\n  height: 100%;\n  background-color: var(--sidebar-color);\n  padding: var(--sidebar-maincontent-padding);\n}\n\n.mainContent {\n  background-color: var(--background-color);\n  width: 81%;\n  height: 100%;\n  padding: var(--sidebar-maincontent-padding);\n}\n\n.homeSideBarHeaderContainer {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  background-color: var(--sidebar-header-background-color);\n  flex-wrap: wrap;\n  margin-bottom: 25px;\n  padding: 10px;\n  border-radius: 10px;\n  border-bottom: 2px solid white;\n  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;\n}\n\n.homeSideBarHeader {\n  padding: 5px;\n}\n\n.homeSideBarHeaderInfo h1 {\n  font-size: 1.5rem;\n  color: var(--primary-color);\n  margin-left: 5px;\n}\n\n.homeSideBarHeaderInfo {\n  display: flex;\n  align-items: center;\n  justify-content: flex-start;\n}\n\n.homeSideBarHeaderIcons {\n  display: flex;\n  align-items: center;\n  justify-content: flex-end;\n}\n\nimg {\n  filter: brightness(0) invert(1);\n}\n\n.homeProfilePicture {\n  width: 30px;\n  height: 30px;\n  border-radius: 50%;\n  margin-right: 10px;\n  filter: brightness(1) invert(0);\n}\n\n.homeBellIcon {\n  margin-right: 8px;\n}\n\n.homeSideBarItem {\n  display: flex;\n  align-items: center;\n  justify-content: flex-start;\n  padding: 6.5px;\n  border-radius: 10px;\n  background-color: var(--sidebar-item-background-color);\n  cursor: pointer;\n  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;\n  transform: scale(1) translateY(0);\n\n  transition: background-color 0.2s ease-in-out, transform 0.2s ease-in-out,\n    box-shadow 0.2s ease-in-out;\n}\n\n.sideBarHeader {\n  display: flex;\n  flex-direction: column;\n  height: 100%;\n}\n\n.homeSideBarItems {\n  display: flex;\n  flex-direction: column;\n  gap: 15px;\n  scrollbar-width: thin;\n  margin: 5px;\n}\n\n.homeSidebarItemText {\n  display: flex;\n  flex-grow: 1;\n  justify-content: center;\n  align-items: center;\n  color: var(--primary-color);\n  font-size: 1.05rem;\n}\n\n.homeSidebarItemIconContainerLeft,\n.homeSidebarItemIconContainerRight {\n  display: flex;\n}\n\n.homeSidebarItemIconContainerRight {\n  gap: 7px;\n}\n\n.homeSideBarItem:hover {\n  background-color: #160927be;\n  transform: scale(1.03) translateY(-2px);\n}\n\n.homeSidebarItemIconContainer {\n  width: 95%;\n  display: flex;\n  position: absolute;\n  justify-content: space-between;\n}\n\n.deleteProjectIcon {\n  padding: 1px;\n  width: 22px;\n  align-items: flex-start;\n  max-height: 22px;\n  cursor: pointer;\n  background-color: #ffffff00;\n  border-radius: 5px;\n  box-shadow: rgba(0, 0, 0, 0.24) 0px 0.5px 5px;\n  display: none;\n\n  transform: scale(1) translateY(0);\n\n  transition: background-color 0.2s ease-in-out, transform 0.2s ease-in-out,\n    box-shadow 0.2s ease-in-out;\n}\n\n.homeSideBarItem:hover .deleteProjectIcon {\n  display: inline-block;\n}\n\n.editProjectIcon {\n  padding: 1px;\n  width: 22px;\n  align-items: flex-start;\n  max-height: 22px;\n  cursor: pointer;\n  background-color: #ffffff00;\n  border-radius: 5px;\n  box-shadow: rgba(0, 0, 0, 0.24) 0px 0.5px 5px;\n  display: none;\n\n  transform: scale(1) translateY(0);\n\n  transition: background-color 0.2s ease-in-out, transform 0.2s ease-in-out,\n    box-shadow 0.2s ease-in-out;\n}\n\n.homeSideBarIt